$serif: "Biaodian Pro Serif GB",et-book,"Numeral LF Serif","Han Songti GB","Noto Serif SC","Noto Serif KR",Batang,Palatino,"Palatino Linotype","Palatino LT STD","Book Antiqua",Georgia,serif,"Noto Emoji";
$kai: "Biaodian Pro Serif GB",et-book,"Numeral LF Serif",Palatino,"Palatino Linotype","Palatino LT STD","Book Antiqua",Georgia,"Times New Roman","Han Kaiti GB",STKaiti,Kaiti,"Kaiti SC",Kai,FZKaiti,"Noto Serif SC","Noto Serif KR",Batang,cursive,serif,"Noto Emoji";
$sans: "Biaodian Pro Sans GB","Gill Sans", "Gill Sans MT", Calibri, "Helvetica Neue", Arial, "Han Heiti GB", "Han Heiti", Heiti, Hei, STHeiti, sans-serif, "Noto Emoji";

/* ------------------------------------------------------------------------ */
/* hugo-tufte.css */
/* Contains extensions to the original tufte.css styles to */
/* accomodate a blog-like site. */
/* ------------------------------------------------------------------------ */

/* ------------------------------------------------------------------------ */
/* Generic content, such as the index list pages */
/* ------------------------------------------------------------------------ */
:root * {
      /*text-align: left;*/
      text-rendering: optimizeLegibility;
      --waline-font-size: 1.2rem;
      --waline-theme-color: #555;
      --waline-active-color: #000;
      --waline-bgcolor-light: none;
}

:focus {
  outline: medium auto currentColor;
  outline: medium auto invert;
  outline: 5px auto -webkit-focus-ring-color;
}

::selection {
    color: #fffff8;
    background-color: #404040;
}

a.heading-anchor {
    display: none;
    visibility: collapse;
}

h2 {
    margin-top: 5.5rem;
}

h2:hover > a.heading-anchor,
h3:hover > a.heading-anchor {
    display: inline;
    visibility: visible;
}

hr {
    text-align: left;
    margin-left: 0;
    margin-top: 1.4rem;
    margin-bottom: 1.4rem;
    // width: 75%;
    max-width: 45rem;
    border-style: solid none none none;
    border-color: #111;
}

kbd {
  border: 1px #111 solid;
  border-radius: 5px;
  padding-right: 2px;
  padding-left: 2px;
}

mark {
    background: #f0d9bb;
}

/* ------------------------------------------------------------------------ */
/* Styling for listing pages. */
/* ------------------------------------------------------------------------ */
.list-page {
    ul {
        list-style-type: none;
        margin: -0.25em;
        width: 87.5%;
        max-width: 45rem;
    }
    li {
        margin: 0;
        /*font-size: 95%;*/
    }
}
.list-page .list-date {
    display: inline;
    font-size: 0.75em;
    /* padding-right: 2em; */
    /* margin-right: 2em; */
}

table:not(.lntable) {
    margin-top: 1.4em;
    font-size: 1.4rem;
    width: auto; /* making booktabs style tables the unstyled default in case someone uses Markdown styling */
    /* margin: 0 auto; */
    /* border-spacing: 0px; */
}

table:not(.lntable) tr th {
  border-bottom: 1px solid #111;
  text-transform: uppercase;
}

table:not(.lntable) tr th,
table:not(.lntable) tr td {
    padding-right: 0.5rem;
}

table.lntable {
    td.lntd {
        padding: 0em;
    }
    border-spacing: 0;
    padding: 0;
}

/* ------------------------------------------------------------------------ */
/* Styling for footnotes. */
/* ------------------------------------------------------------------------ */
.footnotes hr {
    margin-top: 4.4em;
}

.footnotes ol {
    width: 55%;
    margin-top: 4.4rem;

    li {
        p {
            width: 100%;
            margin: 0;
            padding: 0;

            a.footnote-backref {
                font-size: 1.2rem;
                text-decoration: none;
            }
        }
    }
}

/* ------------------------------------------------------------------------ */
/* Styling for maths. */
/* ------------------------------------------------------------------------ */

/* mjx-container[jax="CHTML"][display="true"]::-webkit-scrollbar,
.katex-display::-webkit-scrollbar {
  display: none;
}*/

mjx-container[jax="CHTML"][display="true"],
.katex-display {
      overflow: auto;
      overflow-y: hidden;
/*     -ms-overflow-style: none;
    scrollbar-width: none; */
}


/* ------------------------------------------------------------------------ */
/* Styling for margin/side notes. */
/* ------------------------------------------------------------------------ */
.marginnote .marginnote-ind,
.sidenote .sidenote-number {
    margin-right: 5px;
}

/* prevent over-crowding */

.sidenote, .marginnote {
    margin-bottom: 1rem;
}

.sidenote code,
.marginnote code {
    font-size: 0.9rem;
}

/* ------------------------------------------------------------------------ */
/* Styling & fixes for cols. */
/* ------------------------------------------------------------------------ */
// create grid
.row {
  /* mobile */
  display: grid;
  width: 75%;
  gap: 0.3rem;
  margin: 0 0 1rem 0;
}

.row .column:nth-of-type(2n) {
  margin-left: 1rem;
}

.row .column:nth-of-type(3n) {
  margin-left: 2rem;
}

.row .column > p:first-child {
  margin-top: 0;
}

.row .column > p:last-child {
  margin-bottom: 0;
}

@media (min-width: 760px) {
  /* desktop */
  /* https://stackoverflow.com/a/62086985 */
  .row {
    grid-auto-columns: 1fr;
    grid-auto-flow: column;
    /* above 2 lines are equivalent as below 1 line */
    /* grid-template-columns: repeat(auto-fit, minmax(200px, 1fr)); */
    gap: 1.4rem;
    margin: 0 0 1.4rem 0;
  }
  .row .column {
    margin-left: 0 !important;
  }
}

.column > p {
  width: 100%;
}

.column > p > label.margin-toggle.marginnote-ind {
  display: inline;
}

.column > p > .sidenote,
.column > p > .marginnote {
  display: none;
}

.column > p > .margin-toggle:checked + .sidenote,
.column > p > .margin-toggle:checked + .marginnote {
  display: block;
  float: left;
  left: 1rem;
  clear: both;
  width: 95%;
  margin: 1rem 2.5%;
  vertical-align: baseline;
  position: relative;
}

/* ------------------------------------------------------------------------ */
/* Styling for buttons. */
/* ------------------------------------------------------------------------ */
.btn {
    display: inline-block;
    margin-top: 1.4rem;
}
.btn button {
    font-size: 1.4rem;
}
.btn button .icon {
    font-size: 1.2rem;
}

/* ------------------------------------------------------------------------ */
/* Styling for i18n. */
/* ------------------------------------------------------------------------ */

/* reduce zh & ja font-size to match en */

p:lang(zh),
p:lang(ja), span:lang(ja), {
    font-size: 1.2rem;
}

/*
.sidenote:lang(zh),
.marginnote:lang(zh) {
    font-size: 0.5rem;
}
*/

.sidenote:lang(ja),
.marginnote:lang(ja) {
    font-size: 0.9rem;
}

/* custom */

body {
    color: #414141;
    font-family: $serif;
}

/* hyperlink */

a {
    border-bottom: 1px solid #a4a4a4;
    text-decoration: none;
}

a:hover, {
    color: #000;
    border-bottom: 1.3px solid #000;
}

/* archive page */

.list-title {
    border: none;
    font-size: 1.25rem;
    font-weight: bolder;
    /*line-height: 1.5;*/
}

.list-title:hover {
    color: #000 !important;
    border-bottom: 1.3px solid #000;
}

.list-date, .date {
    color: #9a9891;
}

.year {
    padding-top: 1rem;
}

.list-page ul {
    max-width: 75rem;
}

.link-reverse, footer.page-footer a, .menu li a {
    border: none;
    color: #414141 !important;
}

.link-reverse:hover, footer.page-footer a:hover, .menu li a:hover {
    color: #000 !important;
    border-bottom: 1px solid #a4a4a4;
}

footer.page-footer ul.page-footer-menu {
    margin-top: 2rem !important;
}

.footnotes ol li p, .footnotes ol li p a.footnote-backref {
    font-size: 1rem;
    font-family: "Biaodian Pro Serif GB",et-book,"Numeral LF Serif",Palatino,"Palatino Linotype","Palatino LT STD","Book Antiqua",Georgia,"Times New Roman","Han Kaiti GB",STKaiti,Kaiti,"Kaiti SC",Kai,FZKaiti,"Noto Serif SC","Noto Serif KR",Batang,cursive,serif;
}

/* head */

.subtitle {
    font-style: inherit;
    font-family: $kai;
}

a.go-back {
    border-bottom: none;
    position: absolute;
    left: -3rem;
    color: #9a989155;
    font-family: $sans;
    font-weight: 200;
}

a.go-back:hover, a.comments-arrow:hover {
    color: #000;
}

h1 {
    font-size: 2.2rem;
    font-weight: 600;
    color: #000;
    line-height: 1.5;
}

/* headings and deco */

h2 {
    font-family: $sans !important;
    font-weight: 600;
    line-height: 1.5;
}

article blockquote {
    padding-left: 10px;
    border-left: 1px solid #ccc;
}

blockquote {
    color: #707070;
    font-family: $kai;
}

em {
    font-style: inherit;
}

li {
    font-size: 1.2rem;
    line-height: 1.5;
}

li > p, li > blockquote > p, li > blockquote > blockquote > p, li > blockquote > blockquote > blockquote > p, li > blockquote > blockquote > blockquote > blockquote > p, li > blockquote > blockquote > blockquote > blockquote > blockquote > p, li > blockquote > blockquote > blockquote > blockquote > blockquote > blockquote > p {
    width: inherit;
}

li>ol {
    width: 100%;
}

/* code */

h2 > code {
    background: #e4e4e485;
    margin: 0.2rem;
    font-size: 1.8rem;
}

.highlight {
    width: 80% !important;
}

p > code, li > code, h-char > code {
    background: #e4e4e485;
    margin: 0.2rem;
}

.highlight>.chroma>code {
    width: 95% !important;
    white-space:pre-wrap;
    word-break:break-all;
    word-wrap:break-word;
}

code {
    font-size: 1rem;
    white-space: pre-wrap;
    // word-break: break-all;
    // word-wrap: break-word;
}

pre.chroma {
    overflow-x: auto;
}


strong {
    color: #000;
    font-size: 1.15rem;
    font-family: $sans;
    /*vertical-align: middle;*/
}

.right-date {
    text-align: right;
}

iframe {
    width: 58% !important;
    max-width: 58% !important;
}

footer.page-footer p {
    font-size: 1rem !important;
}

rt {
    font-size: 0.7em;
}

/* comments */

div#waline {
    width:  55%;
    font-family: "IBM Plex Sans", Aptos, -apple-system, BlinkMacSystemFont, "Helvetica Neue", Helvetica, Arial, "Arial Unicode MS", sans-serif, "Noto Color Emoji";
}

.wl-panel {
    background: none !important;
    background-color: unset !important;
    border-radius: 0 !important;
    margin: 1rem 0 1rem 0 !important;
}

.wl-count {
    display: none !important;
}

.wl-header {
    border-bottom: 1px solid var(--waline-border-color);
}

.wl-header input {
    font-size: 0.85rem !important;
}

.wl-btn {
    border-radius: 0;
}

[data-waline] ol, [data-waline] ul {
    margin-inline-start: 0 !important;
}

details > summary {
    display: block;
    list-style: none;
}

summary::-webkit-details-marker {
    display: none;
}

.comments {
    padding: 0;
    margin: 0;
    display: inline;
    position: relative;
}

h3.comments {
    left: -2rem;
}

.comments:focus {
    outline: none;
}

a.comments-arrow {
    border-bottom: none;
    position: relative;
    left: -3rem;
    color: #9a989155;
    font-family: $sans;
    font-weight: 200;
    font-size: 2rem;
}

// img {
//     border: 10px solid rgba(134, 134, 134, 0.096);
// }

@media screen and (max-width: 760px) {
    h1 {
        width: 95%;
    }
    h2, h3, p, footer {
        width: 85%;
    }
    article li, article p {
        text-align: left !important;
    }  
    iframe {
        width: 85% !important;
        max-width: 85% !important;
        overflow: hidden;
    }
    .sidenote, .marginnote {
        margin: 1.3em;
        font-size: smaller;
    }
    .footnotes ol {
        width: 90%;
    }
    .list-page ul {
        width: 95%;
    }
    .highlight>.chroma>code {
        width: 95% !important;
        white-space: unset !important;
        word-break: unset !important;
        word-wrap: unset !important;
    }
    div#waline {
        width:  85%;
    }
    code {
        // word-break: break-all;
        word-wrap: break-word;
    }
}

@media screen and (min-width: 1080px) {
    strong {
        position: relative;
        top: 0.08rem;
    }
}